@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  font-family: "Public Sans", sans-serif;
  font-size: 1.125rem;
  font-weight: 300;
  color: hsl(233deg, 8%, 62%);
}
@media (min-width: 64em) {
  body {
    font-size: 1.25rem;
  }
}

h1, h2, h3 {
  margin: 0;
}

a, a:visited, a:hover {
  text-decoration: none;
  font-weight: 400;
}

button, .button {
  display: inline-block;
  font-size: 0.875rem;
  text-transform: capitalize;
  padding: 0.875rem 2.1875rem;
  background: linear-gradient(to right, hsl(136deg, 65%, 51%), hsl(192deg, 70%, 51%));
  border-radius: 50px;
  cursor: pointer;
  border: 0;
  color: hsl(0deg, 0%, 100%);
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

@media (max-width: 63.9375em) {
  .hide-in-mobile {
    display: none;
  }
}

@media (min-width: 48em) {
  .hide-in-desktop {
    display: none;
  }
}

.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}

.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;
}
.container--pall {
  padding: 2.25rem 1.5rem;
}
.container--py {
  padding: 2.25rem 0;
}
.container--px {
  padding: 0 1.5rem;
}
.container--pt {
  padding-top: 2.25rem;
}
.container--pb {
  padding-bottom: 2.25rem;
}
.container--pl {
  padding-left: 1.5rem;
}
.container--pr {
  padding-right: 1.5rem;
}

.header.open .header__menu > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__menu > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__menu > span:last-child {
  transform: rotate(-45deg);
}
.header.open .overlay {
  visibility: visible;
}
.header .overlay {
  visibility: hidden;
  position: fixed;
  z-index: 3;
  inset: 0;
  top: 60px;
  background-image: linear-gradient(hsl(233deg, 26%, 24%), transparent);
}
.header nav {
  padding-top: 17px;
  padding-bottom: 17px;
}
.header__logo img {
  width: 8.8125rem;
  height: 1.375rem;
}
.header__menu > span {
  display: block;
  width: 26px;
  height: 2px;
  background-color: hsl(233deg, 26%, 24%);
  transform-origin: 3px 1px;
  transition: all 300ms ease-in-out;
}
.header__menu > span:not(:last-child) {
  margin-bottom: 5px;
}
.header__links a {
  font-size: 0.875rem;
  color: hsl(233deg, 8%, 62%);
  transition: color 300ms ease-in-out;
  position: relative;
}
.header__links a:not(:last-child) {
  margin-right: 2rem;
}
.header__links a::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 5px;
  left: 0;
  right: 0;
  bottom: -30px;
  background: linear-gradient(to right, hsl(136deg, 65%, 51%), hsl(192deg, 70%, 51%));
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__links a:hover {
  color: hsl(233deg, 26%, 24%);
}
.header__links a:hover::before {
  opacity: 1;
}
.header__cta {
  transition: opacity 300ms ease-in-out;
}
.header__cta:hover {
  opacity: 0.75;
}
.header__navlinks {
  background-color: hsl(0deg, 0%, 100%);
  margin-top: 7px;
  border-radius: 5px;
}
.header__navlinks a {
  display: block;
  text-align: center;
  color: hsl(233deg, 26%, 24%);
  padding: 0.625rem;
}

.hero {
  background-color: hsl(0deg, 0%, 98%);
}
@media (min-width: 64em) {
  .hero .container {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }
}
.hero .container > *:first-child {
  flex: 3;
}
.hero .container > *:last-child {
  flex: 2;
}
.hero__image {
  background-image: url(../images/bg-intro-mobile.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  min-height: 17.5rem;
  position: relative;
}
.hero__image::before {
  content: "";
  display: block;
  position: absolute;
  background-image: url("/images/image-mockups.png");
  background-size: 93%;
  background-repeat: no-repeat;
  background-position: center bottom;
  width: 100%;
  height: 100%;
}
@media (min-width: 64em) {
  .hero__image {
    background: none;
    height: 41rem;
    position: relative;
  }
  .hero__image::before {
    content: "";
    display: block;
    position: absolute;
    background-image: url("/images/bg-intro-desktop.svg");
  }
  .hero__image::after {
    content: "";
    position: absolute;
    display: block;
    background-image: url("/images/image-mockups.png");
    background-size: 93%;
    background-repeat: no-repeat;
    background-position: center bottom;
    width: 100%;
    height: 100%;
  }
}
.hero__text {
  color: hsl(233deg, 26%, 24%);
  text-align: center;
}
.hero__text h1 {
  font-weight: 300;
  font-size: 2.3125rem;
  line-height: 1.15;
  margin-bottom: 1.5rem;
}
@media (min-width: 64em) {
  .hero__text {
    text-align: left;
  }
  .hero__text h1 {
    font-size: 3.25rem;
  }
}
.hero__content {
  line-height: 1.5;
  margin: 0 0 2.25rem;
}